<style>
#profile-main-container{
    background-color: #f6f9fa;
    height: 1150px;
    width: 100%;
}
#profile-head{
    background-color: white;
    width: 1000px;
    height: 300px;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    margin: 0 auto;
}
#back-img-wrap{
    width: 1000px;
    height: 170px;
    background-color: black;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
}
#back-img{
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 auto;
}
#user-info-container{
    position: absolute;
    left: 0;
    bottom: 0;
    margin: 25px;
}
#user-avatar-wrap{
    float: left;
}
#user-avatar{
    border-radius: 50%;
    border: 5px;
    border-color: black;
}
#user-text-wrap{
    width: 600px;
    float: left;
    margin-left: 25px; 
}
#user-name{
    color: black;
    font-weight: bold;
    font-size: 25px;
