    float: left;
}
#user-vip{
    text-align: left;
    margin-top: 15px;
    color: #fff;
    font-size: 12px;
    font-weight: 500;
    border-radius: 3px;
    background-color: #f36392;
    float: left;
    margin-left: 15px;
}
#user-sign{
    margin-top: 20px;
    font-size: 14px;
    text-align: left;
    position: absolute;
    bottom: 0;
    left: 105px;
}
#user-sign-content{
    float: left;
    margin-left: 30px;
}
#profile-body-container{
    background-color: white;
    width: 960px;
    height: 750px;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 320px;
    margin: 0 auto;
    padding: 20px;
}
#profile-body-head{
    font-size: 25px;
    color: black;
    text-align: left;
    border-bottom: 1px solid #ddd;
    margin-bottom: 30px;
}
#profile-body-body{
    color: black;
    height: 600px;
}
#profile-bangumi-item-left{
    width: 470px;
    height: 144px;
    margin-bottom: 30px;
    margin-right: 20px;
    float: left;
    display:block;
}
#profile-bangumi-item-right{
    width: 470px;
    height: 144px;
    margin-bottom: 30px;
    float: left;
    display: block;
}
