    font-size: 18px;
    max-height: 48px;
    line-height: 24px;
    margin: -3px 0 10px;
    color: #222;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
}
#profile-bangumi-title:hover{
  color:cornflowerblue;
}
#profile-detail-intro{
    width: 320px;
    text-align: left;
    font-weight: 400;
    font-size: 12px;
    color: #222;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 57px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}
</style>

<script>
import $ from 'jquery'

export default {
    name:'profile',
    data(){
        return{
            note:"",
            editsign: "",
            vip:"",
            animechased: {},
            more:{
                "vertical-align": "-6px",
                backgroundPosition: "-310px -404px",
                border: "1px solid #b8c0cc",
                color: "#6d757a",
                "font-size": "12px",
                "line-height": "22px",
                "border-radius": "4px",
                "padding": "0 19px 0 10px",
                "font-weight": "400",
                "font-family": "initial",
                top: "20px",
                right: "19px",
                width: "24px",
                height: "24px",
                position: "absolute",
                "background-repeat": "no-repeat",
                backgroundImage: "url(" + require("@/assets/iconstwo.png") + ")",
            },
            editicon:{
                "vertical-align": "-6px",
                backgroundPosition: "-1428px -340px",
                width: "24px",
                height: "24px",
                "margin-top": "12px",
                "margin-left": "0px",
                "margin-right": "7px",
                "background-repeat": "no-repeat",
                backgroundImage: "url(" + require("@/assets/icons.png") + ")",
            },
        }
    },

    methods:{
        ___trans:function(s){
        if(s==null) return '0'
        return s.replace(/[#:\/\\?*<>"| ()]/g,'_')
        },
        working:function(e){
            this.$message({
                type:'info',
                message:'施工中~'
            })
        },
        mouseonMore:function(){
            document.getElementById('more').style.backgroundPosition = "-438px -404px"
            document.getElementById('more').style.borderColor = "#00a1d6"
            document.getElementById('more').style.color = "#00a1d6"
        },
        mouseoffMore:function(){
            document.getElementById('more').style.backgroundPosition = "-310px -404px"
            document.getElementById('more').style.borderColor = "#b8c0cc"
            document.getElementById('more').style.color = "#6d757a"
        },
        editprofile:function(){
            this.$prompt("你的个人签名：",{
                confirmButtonText: '提交',
                }).then(({value}) => {
                    this.editsign = value
                }).then(() => {
                    fetch(this.$store.state.backend + '/editSign',{
                        method:'POST',
                        mode:'cors',
                        body: JSON.stringify({
                            'username':localStorage.getItem('username'),
                            'sign':this.editsign,
                        })
                  }).then(res => res.json())
                    .then(data => {
                        document.getElementById("user-sign-content").innerHTML = data['sign']
                  }).catch(err => {
                    this.$message({
                        type:'info',
                        message:'网络连接失败'
                    })
                  })
                })
        },
        addAnimeLink:function(){
            var that = this
            $('#profile-body-body').on("click", ".profile-bangumi-img", function(e){
                fetch(that.$store.state.backend + '/postName',{
                    method:'POST',
                    mode:'cors',
                    body:JSON.stringify({
                        'name' : that.animechased[e.target.getAttribute('value')][0]
                    })
                }).then(that.$router.push('detail'))
            })
            $('#profile-body-body').on("click", "#profile-bangumi-title", function(e){
                fetch(that.$store.state.backend + '/postName',{
                    method:'POST',
                    mode:'cors',
                    body:JSON.stringify({
                        'name' : e.target.innerHTML
                    })
                }).then(that.$router.push('detail'))
            })
        }
    },

    created(){
        //this.note = localStorage.getItem('username')
        fetch(this.$store.state.backend + '/loadFanSpace',{
            method:'GET',
            mode:'cors'
        }).then(res => res.json())
          .then(data =>{
              this.note = data['nickname']
              if(localStorage.getItem('username') != data['nickname']){
                  document.getElementById('edit').hidden = true;
                  document.getElementById('user-sign-content').style.marginLeft = "0px"
              }
              document.getElementById("user-sign-content").innerHTML = data['sign']
              if(data['face']){
                  document.getElementById("user-avatar").src = "data:image/png;base64," + data['face']
              }
              this.vip = data['vip']
              this.animechased = data['animechased']
              //追的番
              var code = ''
              var bangumi_id=''
              var animechase = data['animechased'][0]
              if(animechase > 8) {data['animechased'][0] = 8}
              for(var i=1; i<=data['animechased'][0]; i++){
                    if(i%2 == 1){
                        bangumi_id="profile-bangumi-item-left"
                    }
                    else{
                        bangumi_id="profile-bangumi-item-right"
                    }
                    code += '<div id='+ bangumi_id +'> \
                                <div id="profile-bangumi-img-wrap"> \
                                    <img id="profile-bangumi-img" class="profile-bangumi-img" src="" value='+ i.toString() +' width="110px" height="144px"> \
                                </div> \
                                <div id="profile-bangumi-detail"> \
                                    <div id="profile-bangumi-title" title='+ data['animechased'][i][0] +'>'+ data['animechased'][i][0] +'</div> \
                                    <p id="profile-detail-intro">'+ data['animechased'][i][1] +'</p> \
                                </div> \
                            </div>'
              }
              document.getElementById("profile-body-body").innerHTML = code
              var x = document.getElementsByClassName('profile-bangumi-img')
              for(var i=0; i<x.length; i++){
                  x[i].src = require("@/assets/animeImg/"+ this.___trans(data['animechased'][i + 1][0]) +".png")
              }
              // todo:绑定anime链接
              this.addAnimeLink()
              window.scrollTo(0,0)
          })
    }
}
</script>


